@font-face {
  font-family: "roboto_condensedregular";
  src: url("../fonts/robotocondensed-regular-webfont.woff2") format("woff2"), url("/fonts/robotocondensed-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "roboto_condensedbold";
  src: url("../fonts/robotocondensed-bold-webfont.woff2") format("woff2"), url("/fonts/robotocondensed-bold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "roboto_condensedbold_italic";
  src: url("../fonts/robotocondensed-bolditalic-webfont.woff2") format("woff2"), url("/fonts/robotocondensed-bolditalic-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "roboto_condenseditalic";
  src: url("../fonts/robotocondensed-italic-webfont.woff2") format("woff2"), url("/fonts/robotocondensed-italic-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "roboto_condensedlight_italic";
  src: url("../fonts/robotocondensed-lightitalic-webfont.woff2") format("woff2"), url("/fonts/robotocondensed-lightitalic-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "roboto_condensedlight";
  src: url("../fonts/robotocondensed-light-webfont.woff2") format("woff2"), url("/fonts/robotocondensed-light-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
html {
  font-size: 16px;
  color: #000;
}

body {
  font-family: "roboto_condensedregular", "Arial", sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto;
  background: #e9eae2;
}

.header {
  display: grid;
  grid-template-columns: repeat(16, 1fr);
}
.header__left {
  grid-column: span 11;
  padding-left: 15.9%;
  padding-top: 4.8%;
  padding-bottom: 8.7%;
  background: url("../../img/image1.png") 100% center no-repeat;
  background-size: 100%;
}
@media (min-resolution: 2dppx), (min-width: 1500px) {
  .header__left {
    background-image: url("../../img/image1-2x.png");
  }
}
.header__right {
  grid-column: span 5;
  background-color: #7bcbc1;
  position: relative;
  padding-top: 10.7%;
}
.header__right::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url('data:image/svg+xml;utf8, <svg xmlns="http://www.w3.org/2000/svg"><line x1="0" y1="100%" x2="100%" y2="0" stroke="rgb(255,255,255)" stroke-width="1" /></svg>');
}
.header__logo {
  display: block;
  max-width: 100%;
}
.header__slogan {
  font-size: 18px;
  text-transform: uppercase;
  line-height: 1;
  color: #333333;
  margin: 0;
  margin-top: 5px;
}
@media (min-width: 1500px) {
  .header__slogan {
    /*
    $minSizePercent is a (local font size / global font size) * 100%;
    $maxSize is a (local font size * 2);
    $minSize is a local font;
    $minWidth is a min-width in px;
    $maxWidth is a (min-width * 2);
    $minWidth is a min-width;
    */
    font-size: calc(112.5% + (36 - 18) * (100vw - 1500px) / (3000 - 1500));
  }
}
.header__socials {
  margin-top: 60px;
  margin-left: 15px;
  margin-bottom: 15px;
}
.header__address {
  font-size: 13px;
  color: #fff;
  text-transform: uppercase;
  line-height: 1.077;
  position: absolute;
  left: 12.9%;
  bottom: 36.5%;
}
@media (min-width: 1500px) {
  .header__address {
    /*
    $minSizePercent is a (local font size / global font size) * 100%;
    $maxSize is a (local font size * 2);
    $minSize is a local font;
    $minWidth is a min-width in px;
    $maxWidth is a (min-width * 2);
    $minWidth is a min-width;
    */
    font-size: calc(81.25% + (26 - 13) * (100vw - 1500px) / (3000 - 1500));
  }
}
.header__contacts {
  font-size: 13px;
  color: #fff;
  text-transform: uppercase;
  line-height: 1.077;
  position: absolute;
  left: 29%;
  bottom: 20%;
}
@media (min-width: 1500px) {
  .header__contacts {
    /*
    $minSizePercent is a (local font size / global font size) * 100%;
    $maxSize is a (local font size * 2);
    $minSize is a local font;
    $minWidth is a min-width in px;
    $maxWidth is a (min-width * 2);
    $minWidth is a min-width;
    */
    font-size: calc(81.25% + (26 - 13) * (100vw - 1500px) / (3000 - 1500));
  }
}
.header__tel, .header__mail {
  text-decoration: none;
  color: #fff;
  line-height: 1.077;
  display: inline-block;
  transition-property: color;
  transition-duration: 500ms;
}
.header__tel:hover, .header__mail:hover {
  color: #f8c255;
}

@media (max-width: 980px) {
  .header__left {
    grid-column: span 9;
  }
  .header__right {
    grid-column: span 7;
  }
}
@media (max-width: 560px) {
  .header {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }
  .header__left {
    grid-column: span 1;
    grid-row: 2/3;
  }
  .header__right {
    grid-column: span 1;
    grid-row: 1/2;
  }
  .header__address {
    position: static;
    margin-left: 10%;
    margin-top: 7%;
  }
  .header__contacts {
    position: static;
    margin-left: 35%;
    margin-top: 20%;
    margin-bottom: 20%;
  }
}
.socials {
  list-style: none;
  padding: 0;
  display: flex;
  align-items: flex-end;
}
.socials__element {
  margin: 0 6px;
}
.socials__element:last-child {
  margin-right: 0;
}
.socials__facebook {
  width: 10px;
  height: 22px;
  fill: #7bcbc1;
}
.socials__twitter {
  width: 26px;
  height: 21px;
  fill: #7bcbc1;
}
.socials__linkedin {
  width: 24px;
  height: 23px;
  fill: #7bcbc1;
}
.socials__google {
  width: 19px;
  height: 19px;
  fill: #7bcbc1;
}